.app {
  height: 100%;
  width: 100%;
}

.app-header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: white;
}

.app--dark {
  background: #121211;
  color: white;
}

.app--light {
  background: whitesmoke;
  color: black;
}

.core-panel {
  padding-left: 4px;
  padding-right: 4px;
  overflow: hidden;
  height: 100%;
}

.core-panel__left-column {
  width: 20vw;
}
.core-panel__right-column {
  width: 20vw;
}
.core-panel__center-column {
  width: 60vw;
}

.core-panel__flex {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: space-between;
}

.panel--right-align {
  text-align: right;
}

.panel--left-align {
  text-align: left;
}
.journal {
  overflow: hidden;

  .journal__entry {
    color: gray;
  }
}

.journal-panel {
  padding-top: 8px;
}

.app--dark .journal__entry:first-of-type {
  color: white;
}

.app--light .journal__entry:first-of-type {
  color: black;
}

.relics-button {
  border-radius: 4px;
  border: gray 1px solid;
  padding: 6px;
  cursor: pointer;
  user-select: none;

  &.relics-button--disabled {
    color: gray;
    cursor: default;
  }
}

.nav-panel {
  height: 36px;
  padding-top: 8px;
}
.nav-button {
  border: gray 1px solid;
  padding: 6px;
  cursor: pointer;
  user-select: none;

  &.nav-button--disabled {
    color: gray;
    cursor: default;
  }
  &.nav-button--selected {
    font-weight: bold;
  }
}


.padded-text {
  padding-top: 4px;
}
html, body, #root {
  height: 100%
}